.contact-container {
  font-family: 'Arial', sans-serif;
  background-color: #f4f7fc;
  padding: 20px;
  border-radius: 10px;
  max-width: 900px;
  margin: 0 auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.title {
  font-size: 2.5rem;
  color: #2c3e50;
  text-align: center;
  margin-bottom: 20px;
}

.address, .phone, .email {
  font-size: 1.2rem;
  color: #34495e;
  text-align: center;
  margin-bottom: 20px;
}

.subheading {
  font-size: 1.8rem;
  color: #2980b9;
  text-align: center;
  margin-bottom: 20px;
}

.map-container {
  display: flex;
  justify-content: center;
}

iframe {
  border-radius: 8px;
}